.youtube-player-container {
  width: 100%;
  position: relative;
  background: #000;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  /* Horizontal rectangle aspect ratio */
  aspect-ratio: 16 / 9;
  max-height: 400px;
}

.youtube-player {
  width: 100%;
  height: 100%;
  border-radius: 12px;
}

.youtube-player iframe {
  border-radius: 12px;
  width: 100%;
  height: 100%;
  max-height: 400px;
}

.youtube-player-placeholder {
  width: 100%;
  height: 400px;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #ccc;
  /* Maintain aspect ratio */
  aspect-ratio: 16 / 9;
  max-height: 400px;
}

.placeholder-content {
  text-align: center;
  padding: 40px;
  max-width: 400px;
}

.placeholder-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  opacity: 0.6;
}

.placeholder-content h3 {
  font-size: 1.5rem;
  color: #555;
  margin-bottom: 10px;
  font-weight: 600;
}

.placeholder-content p {
  color: #777;
  line-height: 1.5;
  font-size: 1rem;
}

/* Mobile styles */
@media (max-width: 768px) {
  .youtube-player-container {
    max-height: 200px;
  }
  
  .youtube-player iframe {
    max-height: 200px;
  }
  
  .youtube-player-placeholder {
    height: 200px;
    max-height: 200px;
  }
  
  .placeholder-content {
    padding: 15px;
  }
  
  .placeholder-icon {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .placeholder-content h3 {
    font-size: 1.1rem;
    margin-bottom: 8px;
  }
  
  .placeholder-content p {
    font-size: 0.85rem;
    line-height: 1.3;
  }
}

/* Small mobile styles */
@media (max-width: 480px) {
  .youtube-player-container {
    max-height: 180px;
    border-radius: 8px;
  }
  
  .youtube-player {
    border-radius: 8px;
  }
  
  .youtube-player iframe {
    max-height: 180px;
    border-radius: 8px;
  }
  
  .youtube-player-placeholder {
    height: 180px;
    max-height: 180px;
    border-radius: 8px;
  }
  
  .placeholder-content {
    padding: 10px;
  }
  
  .placeholder-icon {
    font-size: 2rem;
    margin-bottom: 8px;
  }
  
  .placeholder-content h3 {
    font-size: 1rem;
    margin-bottom: 5px;
  }
  
  .placeholder-content p {
    font-size: 0.8rem;
  }
}
